class Prefix{
    constructor(code,fc,zone,ND,week,e=false){
        this.code=code
        this.fc=fc
        this.zone=zone
        this.ND=ND
        this.week={...week,Sa:false,Su:false}    
        this.exceptions = e
    }
}
class Exception extends Prefix{
    constructor(code,prefix,fc,zone,ND,week,p={},e=false){
        super(prefix,fc,zone,ND,week)
        this.ecode = code
    }
}
